*{

	padding: 0;
	margin: 0;

}

/* Variables */

$background: #fff;
$body-text-color: #000;
$heading-color: #4b5939;
$link-color: #002A00;
$title-color: #9b6026;
$accent-color: #809962;
$link-visited-color: #5c6e46;
$link-hover-color: #8d4709;

$logo-height: 338px;
$logo-width: 465px;

$body-font: "Open Sans", sans-serif;
$heading-font: 'Libre Baskerville', serif;

$heading-margin: 10px 0;

$title-size: 50px;
$h1-size: 26px;
$h2-size: 24px;
$h3-size: 22px;
$p-size: 20px;
$footer-size: 10px;

/* Mix Ins */

/* Core Elements */

/* Structural Elements */
html
{
	max-width: 1200px;
	margin: 0 auto;
}

body
{
	display: grid;
	align-items: center;
	grid-template: 3fr 2fr 3fr 1fr / 1fr 1fr 1fr;
}

header
{
	grid-area: 1 / 1 / span 1 / span 3;
	justify-self: center;

	a
	{	
		align-items: center;
		display: flex;
		flex-direction: row;
		text-decoration: none;

		div
		{
			margin: 10px;
		}
	}
}

nav
{
	grid-area: 2 / 1 / span 1 / span 3;
	justify-self: center;
	width: 100%;

	ul
	{
		display: flex;
		flex-direction: row;
		justify-content: center; 
		list-style: none;

		li
		{
			border: 2pt solid $accent-color;
			flex: 1;
			margin: 0 10px;
			padding: 10px 0;
			text-align: center;
			width: 10%;

			a
			{
				color: $heading-color;
				font: $h3-size $heading-font;
				text-decoration: none;
			}
		}

	}

}

main
{

}

footer
{
	font: $footer-size $heading-font;
	justify-self: center;
	grid-area: -1 / 1 / span 1 / span 3;
}


/* IDs */
#title
{
	align-self: center;
	color: $title-color;
	display: inline-block;
	font: bolder $title-size $heading-font;
	padding-top: 10px;
}

#logo
{
	display: inline-block;
	height: $logo-height / 3;
	width: $logo-width / 3;

}
/* Classes */

.separator
{
	margin: 5px 0;
	width: 100%;
}


/* Media Queries */

@media screen and (max-width: 1200px)
{


}

@media screen and (max-width: 820px)
{
	#title
	{
		font-size: $title-size * 0.75;
	}
}

@media screen and (max-width: 670px)
{
	#title
	{
		font-size: $title-size * 0.70;
	}

	#logo
	{
	display: inline-block;
	height: $logo-height / 4;
	width: $logo-width / 4;

	}

	nav ul li
	{
		margin: 0 5px;
	}

	nav ul li a
	{
		font-size: $h3-size * 0.9;
	}


}

@media screen and (max-width: 600px)
{
	#title
	{
		font-size: $title-size * 0.50;
	}

	nav ul li a
	{
		font-size: $h3-size * 0.7;
	}

}

